body {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  font-family: var(--inconsolata);
  font-size: 1.25rem;
  font-weight: var(--weight-400);
  background-color: var(--button-text);
  color: var(--text-white);
}

:root {
  /*Neutral*/
  --text-white:hsl(0, 0%, 100%);
  --form-border: hsl(252, 6%, 83%);
  --hint-text:hsl(245, 15%, 58%);
  --form-background: hsla(245, 15%, 58%, 0.2);
  --upload-icon:hsla(245, 15%, 58%, 0.3);
  --border:hsl(245, 19%, 35%);
  --button-text:hsl(248, 70%, 10%);
  /*Button*/
  --button:hsl(7, 88%, 67%);
  --button-hover:hsl(7, 71%, 60%);
  /*Gradient*/
  --gradient-text: linear-gradient(
    to right,
    hsl(7, 86%, 67%),
    hsl(0, 0%, 100%)
  );
}

:root {
  /*Font Family*/
  --inconsolata: "Inconsolata", serif;
  /*Font Weights*/
  --weight-400:400;
  --weight-500:500;
  --weight-700:700;
  --weight-800:800;
}

/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}
body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input, button, textarea, select {
  font: inherit;
}

/* 8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper__section {
  width: 100%;
  max-width: 50rem;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.wrapper__form {
  width: 100%;
  max-width: 28.125rem;
}
.wrapper__title {
  line-height: 1.1;
}
@media (min-width: 40em) {
  .wrapper__title {
    font-size: 3.4375rem;
    width: 100%;
  }
  .wrapper__title .break {
    display: block;
  }
}
.wrapper__spot {
  color: var(--hint-text);
}
.wrapper__info {
  border: none;
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: start;
}
.wrapper__input {
  width: 100%;
  background-color: var(--form-background);
  border: 0.125rem solid var(--border);
  border-radius: 0.5rem;
  padding: 0.5rem;
  color: var(--text-white);
}
.wrapper__input::placeholder {
  color: var(--hint-text);
  opacity: 0.7;
}

.label {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 0.625rem;
}

.text-hint {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  font-size: 0.8125rem;
  color: var(--hint-text);
}

fieldset .text-hint {
  display: none;
  color: var(--button-hover);
}

.submit {
  width: 100%;
  background-color: var(--button);
  border: none;
  border-radius: 0.5rem;
  padding: 1rem 0;
  color: var(--button-text);
  font-weight: var(--weight-800);
  margin-top: 1rem;
}

.submit:is(:hover, :focus) {
  background-color: var(--button-hover);
  cursor: pointer;
}

