.wrapper__avatar {
  text-align: start;
}

.upload {
  background-color: var(--form-background);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  margin: 0.5rem 0;
  border: 0.125rem dashed var(--border);
  border-radius: 1rem;
  gap: 1rem;
}
.upload__icon {
  background-color: var(--upload-icon);
  border: 0.0625rem solid var(--border);
  border-radius: 0.5rem;
  padding: 0.5rem;
  width: 100%;
  height: 100%;
  max-width: 5rem;
  max-height: 5rem;
}
.upload__text {
  font-size: 1.125rem;
  color: var(--hint-text);
}
.upload__actions {
  display: none;
}

.button {
  background-color: var(--upload-icon);
  color: var(--hint-text);
  border: none;
  border-radius: 0.3rem;
  text-decoration: underline;
}

.button:is(:hover, :focus) {
  cursor: pointer;
  outline: 0.125rem solid var(--border);
}

.ticket {
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  padding: 1rem;
}
.ticket__title {
  font-size: 1.75rem;
  font-weight: var(--weight-700);
  text-align: center;
  line-height: 1.1;
}
.ticket__nameValue {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.ticket__info {
  font-size: 1.125rem;
  color: var(--hint-text);
}
.ticket__emailValue {
  color: var(--button);
}
.ticket__pattern {
  background-image: url(/assets/images/pattern-ticket.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  max-width: 37.5rem;
  max-height: 17.5rem;
  width: 100%;
  height: 100%;
  aspect-ratio: 3/1.4;
  display: grid;
  grid-template-columns: 12.5% 6.67% 61.67% 19.17%;
  grid-template-rows: 25% 14.29% 17.86% 21.43% 21.43%;
  justify-content: start;
  align-items: center;
}
.ticket__icon {
  background-image: url(/assets/images/logo-mark.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
  aspect-ratio: 3/1.4;
  grid-column: 1/2;
  grid-row: 1/2;
  width: 100%;
  height: 100%;
  justify-self: center;
  align-self: center;
}
.ticket__logo {
  grid-column: 2/4;
  grid-row: 1/2;
  font-size: 1.5625rem;
  font-weight: var(--weight-700);
  align-self: center;
  justify-self: start;
}
@media (min-width: 40em) {
  .ticket__logo {
    font-size: 2.5rem;
  }
}
.ticket__date {
  grid-column: 2/4;
  grid-row: 2/3;
  width: 100%;
  height: 100%;
  font-size: 0.8125rem;
  color: var(--hint-text);
  justify-self: start;
  align-self: center;
  text-align: start;
}
@media (min-width: 40em) {
  .ticket__date {
    font-size: 1.25rem;
  }
}
.ticket__avatar {
  grid-column: 1/3;
  grid-row: 4/6;
  width: 100%;
  height: 100%;
  border-radius: 1.2rem;
  padding: 0.5rem;
}
@media (min-width: 40em) {
  .ticket__avatar {
    padding: 1rem;
    border-radius: 1.5rem;
  }
}
.ticket__name {
  grid-column: 3/4;
  grid-row: 4/5;
  width: 100%;
  height: 100%;
  font-size: 1.125rem;
  font-weight: var(--weight-500);
  align-self: center;
  justify-self: start;
  text-align: start;
  padding-top: 0.5rem;
}
@media (min-width: 40em) {
  .ticket__name {
    font-size: 1.6875rem;
    padding-top: 1.5rem;
  }
}
.ticket__github {
  grid-column: 3/4;
  grid-row: 5/6;
  width: 100%;
  height: 100%;
  font-size: 0.625rem;
  justify-self: start;
  align-self: center;
  text-align: start;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0.5rem;
  color: var(--hint-text);
}
@media (min-width: 40em) {
  .ticket__github {
    font-size: 1.25rem;
    padding-bottom: 1rem;
  }
}
.ticket__code {
  grid-column: 4/5;
  grid-row: 2/5;
  width: 100%;
  height: 100%;
  justify-self: center;
  align-self: center;
  font-size: 0.625rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.ticket__number {
  display: inline-block;
  transform: rotate(90deg);
  font-size: 1.25rem;
  color: var(--hint-text);
}
@media (min-width: 40em) {
  .ticket__number {
    font-size: 2.5rem;
  }
}